	module.exports = {"data":{"site":{"siteMetadata":{"title":"Srinath Muskula","subtitle":"Engineer, writing about topics related to Technology","copyright":"","menu":[{"label":"About me","path":"/about/"},{"label":"Articles","path":"/"},{"label":"Contact","path":"/contact/"}],"author":{"name":"Srinath Muskula","email":"srinath@muskula.xyz","twitter":"redmusk","github":"msrinathr","rss":""},"disqusShortname":"","url":"https://muskula.xyz"}},"markdownRemark":{"id":"/Users/smuskula/Documents/www/lumen/src/pages/articles/2018-04-28/index.md absPath of file >>> MarkdownRemark","html":"<p>I have been learning about different ways to host my blog. Easiest and quick way was to use CMS like Wordpress, this requires a server. Wordpress has lot of features and add-ons to use.\nFor me blog is mostly static posts, you don’t need server to host a static site. <strong>Just waste of resources</strong>. I started looking at famous static site generators like Hugo, Hexo, Jekyll, Pelican.\nThen I found react based Gatsby. Yay!!!</p>\n<p>I will guide you with below steps:</p>\n<ul>\n<li>Setting up Gatsby</li>\n<li>Adding a theme</li>\n<li>Creating a new post</li>\n<li>Push blog to GitHub</li>\n<li>Host blog on GitHub Pages</li>\n<li>Redirect GitHub Pages to a custom domain</li>\n</ul>","fields":{"tagSlugs":["/tags/gatsby/","/tags/web-dev/"]},"frontmatter":{"title":"Creating My Blog Using Gatsby-React","tags":["Gatsby","Web Dev"],"date":"2018-04-28T03:05:00.121Z","description":"I will guide you how to create your blog in using gatsby-react and publish posts with very less effort and use markdown, git or AWS S3 to host blog."}}},"pathContext":{"slug":"/posts/creating-my-blog-gatsby/"}}

